// Proxy plugin.
import * as actionTypes from '../interface/actionTypes'
import * as sagas from './proxyStack'

// Other plugins.
import { LEVELS_CHANGE } from '../../logs/interface/actionTypes'

// Libraries.
import { takeEvery } from 'redux-saga/effects'

export function * setMode (webRTC) {
  yield takeEvery(actionTypes.SET_MODE, sagas.setProxyMode, webRTC)
}

export function * setChannel (webRTC) {
  yield takeEvery(actionTypes.SET_CHANNEL, sagas.setChannel, webRTC)
}

export function * initializeRemote (webRTC) {
  yield takeEvery(actionTypes.INITIALIZE, sagas.initializeRemote, webRTC)
}

/**
 * Taker saga for "change proxy log level" actions.
 * @method updateLogLevelEntry
 * @param  {Object} webRTC The webRTC stack.
 */
export function * updateLogLevelEntry (webRTC) {
  yield takeEvery(LEVELS_CHANGE, sagas.updateProxyLevel, webRTC)
}
