// Subscription plugin.
import * as actionTypes from '../../interface/actionTypes'
import * as actions from '../../interface/actions'
import { DISCONNECT_REASONS, OPERATIONS } from '../../constants'

// State selectors
import { getAuthConfig, getConnectionInfo } from '../../../auth/interface/selectors'
import {
  getSubscriptionInfo,
  getSubscriptionExpiry,
  getWebsocketConfig,
  getPendingOperation
} from '../../interface/selectors'

// Constants
import { platforms } from '../../../constants'

// Requests
import { subscribe, unsubscribe, resubscribe } from '../requests/subscriptions'

// Helpers.
import { logManager } from '../../../logs'

// Libraries.
import {
  actionChannel,
  call,
  cancel,
  cancelled,
  delay,
  fork,
  put,
  race,
  select,
  take,
  takeEvery
} from 'redux-saga/effects'
import { union, isEmpty, difference } from 'lodash/fp'

// Other plugins.
import { connectWebsocket, disconnectWebsocket } from '../../../connectivity/interface/effects'
import { getConnectionState, getConnectivityConfig } from '../../../connectivity/interface/selectors'
import * as connectivityActionTypes from '../../../connectivity/interface/actionTypes'
import { NOTIFICATION_RECEIVED } from '../../../notifications/interface/actionTypes'
import { updateHmacToken } from '../../../auth/interface/actions'

// Errors
import BasicError, { authCodes } from '../../../errors'

// This is an Link plugin.
const platform = platforms.LINK
const log = logManager.getLogger('SUBSCRIPTION')

/**
 * Register the all service for subscriptions.
 * @method registerServices
 */
export function * registerServices () {
  yield put(actions.registerService(['call', 'Presence', 'IM']))
}

/**
 * Entry point for ALL subscription change actions.
 * Queues all subscription changes (ie. subscribe, unsubscribe) to avoid async
 * scenarios. This ensures channels and subscriptions are not duplicated
 * and that resources are created/clean-up when needed.
 * @method subscriptionFlow
 */
export function * subscriptionFlow () {
  // Use a channel to queue ALL subscription change requests.
  const subscriptionChannel = yield actionChannel([actionTypes.SUBSCRIBE, actionTypes.UNSUBSCRIBE])

  while (true) {
    const action = yield take(subscriptionChannel)

    if (action.type === actionTypes.SUBSCRIBE) {
      // Check if we have an existing subscription
      const subscription = yield select(getSubscriptionInfo)
      let task

      if (!subscription || !subscription[0]) {
        // No existing subscription, make a new subscription request
        // Fork off and try to connect/subscribe
        task = yield fork(doSubscribe, action)
      } else {
        // Existing subscription found, update our subscription.
        // Fork off and try to update subscription

        task = yield fork(updateSubscription, action, true)
      }

      // Take the actions for if an error or disconnect occurs
      const finishOrError = yield take([actionTypes.SUBSCRIBE_FINISHED, actionTypes.UNSUBSCRIBE])

      /**
       * This if block covers 2 of the 3 flows of subscribe / unsubscribe.
       * 1. The first block covers what happens when we receive an UNSUBSCRIBE action before we finish connecting.
       *        IE UNSUBSCRIBE comes before a SUBSCRIBE_FINISHED.
       * 2. The second scenario covers what happens when we get a successful SUBSCRIBE_FINISHED event before any UNSUBSCRIBE events.  We just finish (and restart the saga).
       * 3. The third scenario is not explicitly covered here. That scenario is when we received a SUBSCRIBE_FINISHED event with an error before any unsubscribe events.
       *      In this case, there is nothing that we explicitly have to do, since a SUBSCRIBE_FINISHED event with an error doesn't place anything into the state.
       *      Therefore there is no real cleanup for us to do in this scenario. We then want to wait for the next SUBSCRIBE ( which we do via the next iteration of the while loop).
       **/
      if (finishOrError.type === actionTypes.UNSUBSCRIBE) {
        yield cancel(task)
      }
    } else if (action.type === actionTypes.UNSUBSCRIBE) {
      // Check if we have an existing subscription
      const subscription = yield select(getSubscriptionInfo)

      if (subscription && subscription[0]) {
        // Remove all services from our subscribed services if they are present
        const updatedServices = subscription[0].service.filter(service => !action.payload.services.includes(service))

        if (updatedServices.length === 0) {
          // All services are being unsubscribed
          yield call(doUnsubscribe)
        } else if (isEmpty(difference(subscription[0].service, updatedServices))) {
          // We don't have a subscription for any of the services requesting unsubscribing
          // No subscription found
          const error = new BasicError({
            message: `No subscription found for ${JSON.stringify(action.payload.services)}, can't unsubscribe.`,
            code: authCodes.LINK_UNSUBSCRIBE_FAIL
          })

          yield put(actions.unsubscribeFinished({ error: error }))
          log.debug(`Unsubscribe failed: ${error.message}`)
        } else {
          // Some services are being unsubscribed, update our subscription
          action.payload.services = updatedServices.map(service => {
            return {
              service: service
            }
          })

          yield call(updateSubscription, action, false)
        }
      } else {
        // No subscription found
        const error = new BasicError({
          message: "No subscription found, can't unsubscribe.",
          code: authCodes.LINK_UNSUBSCRIBE_FAIL
        })

        yield put(actions.unsubscribeFinished({ error: error }))
        log.debug(`Unsubscribe failed: ${error.message}`)
      }
    }
  }
}

/**
 * Link subscribe function.
 * Performs the workflow of connecting/subscribing to SPiDR services
 * @method doSubscribe
 */
export function * doSubscribe (action) {
  if (!action.payload.services || action.payload.services.length === 0) {
    // Error scenario: No services specified.
    // TODO: Better error.
    yield put(actions.subscribeFinished({ error: true }))
    return
  }

  try {
    // Retrieve the connection info
    const authConfig = yield select(getAuthConfig)

    // Retrieve the connection info.
    const linkConnection = yield select(getConnectionInfo)

    // Get the configured expiry value for subscriptions
    const expires = yield select(getSubscriptionExpiry)

    let { services, ...options } = action.payload

    services = services.map(subscription => subscription.service)

    const response = yield call(subscribe, authConfig, expires, linkConnection, services, options)

    if (response.error) {
      yield put(actions.subscribeFinished({ error: response.error }))
      log.debug(`Subscription failed: ${response.error}`)
      return
    }

    const hmacToken = linkConnection.requestOptions.headers['x-token']

    // If used HMAC token for authentication
    if (hmacToken) {
      // Subsequent requests must not have the x-token header and must have the x-session header.
      // Need to delete the x-token header for subsequent requests to work.
      if (response.subscriptionParams) {
        yield put(updateHmacToken(response.subscriptionParams.sessionId, platform))
      } else {
        const error = new BasicError({
          message: 'Failed user subscription.',
          code: authCodes.LINK_SUBSCRIBE_FAIL
        })
        yield put(actions.subscribeFinished({ error }))
      }
    }

    // Keep track of service subscription information.
    const subscription = {
      ...response.subscriptionParams,
      servicesInfo: response.servicesInfo,
      url: response.subscription
    }

    const { webSocketOAuthMode } = yield select(getConnectivityConfig)

    const params = {}
    const bearerAccessToken = linkConnection.bearerAccessToken
    const accessToken = linkConnection.accessToken

    if (bearerAccessToken && webSocketOAuthMode === 'query') {
      // If using bearerAccessToken, set the param token property to its value
      params.access_token = bearerAccessToken
    } else if (accessToken) {
      // If using an access token from UC, set the property. Note it's different than bearerAccess
      params.token = accessToken
    }

    // Need to create a new object, rather than adding `url` to
    // connectionInfo in order to prevent state mutation.
    // Format the response to pass off to the connectivity plugin.
    const websocketConfig = yield select(getWebsocketConfig)
    const websocketInfo = {
      protocol: 'wss',
      server: websocketConfig.server,
      port: websocketConfig.port,
      url: subscription.notificationChannel,
      params: params
    }

    // Request the websocket connection.
    const wsOpenOrError = yield connectWebsocket(websocketInfo, platform)
    if (wsOpenOrError.error) {
      // Clean up our subscription on KandyLink
      yield call(doUnsubscribe, subscription)

      throw wsOpenOrError.payload
    }

    yield put(actions.subscribeFinished({ subscriptions: [subscription] }, platform))
    log.info(`Subscribed to the following services: ${subscription.service}`)
  } catch (error) {
    yield put(actions.subscribeFinished({ error }))
    log.debug(`Subscription failed: ${error.message}`)
  } finally {
    if (yield cancelled()) {
      yield call(doUnsubscribe)
      // If we enter this point we know we got disconnect mid connection attempt BEFORE SUBSCRIBE_FINISHED fired.
      // Therefore we have to put any special store cleanup code in here.
      // SUBSCRIBE_FINISHED action handles clearing up the store so no logic needs to be here right now.
    }
  }
}

/**
 * Link unsubscribe function.
 * Performs the actions for actually unsubscribing from SPiDR.
 * @method doUnsubscribe
 */
export function * doUnsubscribe (subscription) {
  if (!subscription) {
    // Retrieve needed info from the store.
    subscription = yield select(getSubscriptionInfo)
    // Since link will only ever have 1 subscription object in
    // this array, we can take the first element and use it directly
    subscription = subscription[0]
  }
  const connection = yield select(getConnectionInfo)

  // If the above info is not present, we probably got a disconnect mid-connection attempt.
  if (subscription && connection) {
    // If the info is in the store we can go ahead and unsubscribe.
    // We don't care about the return value, because we are going to clean-up
    //    the subscription in state either way.
    yield call(unsubscribe, connection, subscription.url)
  }

  const wsState = yield select(getConnectionState, platform)
  if (wsState.connected) {
    // disconnect from the websocket
    yield disconnectWebsocket(undefined, platform)
  }

  yield put(actions.unsubscribeFinished({}, platform))
  log.info('Successfully unsubscribed from all services.')
}

/**
 * Saga for extending a user's subscription.
 * When triggered, make a resub request to SPiDR for a specific subscription.
 * @method extendSubscription
 */
export function * extendSubscription () {
  // Resubscription timer is triggered by a successful subscription or a previous resub finishing.
  // Use a channel to queue the triggers (so it can catch the put resub action at the end).
  const resubTriggers = yield actionChannel([actionTypes.SUBSCRIBE_FINISHED, actionTypes.RESUBSCRIPTION_FINISHED])

  while (true) {
    const action = yield take(resubTriggers)

    // If the action was a subscription error, ignore it.
    if (action.type === actionTypes.SUBSCRIBE_FINISHED && action.error) {
      continue
    }

    const connection = yield select(getConnectionInfo)
    const subscription = yield select(getSubscriptionInfo)

    // Check if we actually have a subscription and if not, just continue
    if (!subscription || !subscription[0]) {
      continue
    }

    let attemptNum
    // If the action was a failed resubscription, increment the attempt number.
    if (action.type === actionTypes.RESUBSCRIPTION_FINISHED && action.error) {
      attemptNum = action.payload.attemptNum + 1
    } else {
      attemptNum = 1
    }

    // Set the delay to be halfway between now and when the subscription expires.
    let resubDelay = (subscription[0].expires * 1000) / Math.pow(2, attemptNum)
    // Don't try to resub more often than every 5 minutes.
    resubDelay = resubDelay > 30000 ? resubDelay : 30000

    // Wait for either the resub delay or a disconnect action.
    const { expiry } = yield race({
      expiry: delay(resubDelay),
      unsubscribe: take(actionTypes.UNSUBSCRIBE_FINISHED)
    })

    // If the resubDelay expired, attempt resubscription
    if (expiry) {
      const response = yield call(resubscribe, connection, subscription)

      if (response.error) {
        yield put(
          actions.resubscribeFinished(
            {
              error: response.error,
              attemptNum
            },
            platform
          )
        )
      } else {
        // TODO: Check response.serviceInfo for full/partial resubscription.
        yield put(actions.resubscribeFinished({ attemptNum }, platform))
      }
    }
  }
}

/**
 * Saga for updating a subscription's services.
 * @method updateSubscription
 */
export function * updateSubscription (action, addService = true) {
  const connection = yield select(getConnectionInfo)
  const subscription = yield select(getSubscriptionInfo)

  const services = action.payload.services.map(subscription => subscription.service)

  if (addService) {
    subscription[0].service = union(services, subscription[0].service)
  } else {
    subscription[0].service = subscription[0].service.filter(service => services.includes(service))
  }

  // Use the resubscribe request to update the subscription. The endpoint is for both.
  const response = yield call(resubscribe, connection, subscription)

  if (response.error) {
    const error = new BasicError({
      message: response.message,
      code: authCodes.LINK_UPDATE_SUBSCRIPTION_FAIL
    })

    yield put(actions.subscribeFinished({ error }))
    log.debug(`Subscription failed: ${error.message}`)
  } else {
    // Keep track of service subscription information.
    const updatedSubscription = {
      ...response.subscriptionParams,
      servicesInfo: response.servicesInfo,
      url: response.subscription
    }

    yield put(actions.subscribeFinished({ subscriptions: [updatedSubscription] }, platform))
    log.info(`Subscribed to the following services: ${subscription[0].service}`)
  }
}

/**
 * Saga that handles a "websocket overridden" notification.
 * Treat the notification similarly to how we treat "subscription gone" notification.
 * @method onWebsocketnOverridden
 */
export function * onWebsocketOverridden () {
  // Redux-saga take() pattern.
  // Take notifications about the websocket being 'overridden'.
  function takeOverriddenNotification (action) {
    return (
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.notificationMessage &&
      action.payload.notificationMessage.eventType === 'websocketOverridden'
    )
  }

  while (true) {
    yield take([takeOverriddenNotification])

    // Dispatch an action to disconnect the websocket (and let the connectivity
    //      plugin know we expect it to be disconnected).
    const wsState = yield select(getConnectionState, platform)
    if (wsState.connected) {
      yield disconnectWebsocket(undefined, platform)
    }

    // Dispatch a disconnect finished action to trigger "user disconnected" logic.
    yield put(actions.unsubscribeFinished({ reason: DISCONNECT_REASONS.WS_OVERRIDDEN }, platform))
  }
}

/**
 * Saga that handles a "subscription gone" notification.
 * Treat the notification as a forced disconnect.
 * @method onSubscriptionGone
 */
export function * onSubscriptionGone () {
  // Redux-saga take() pattern.
  // Take notifications about the subscription being 'gone'.
  function takeGoneNotification (action) {
    return (
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.notificationMessage &&
      action.payload.notificationMessage.eventType === 'gone'
    )
  }

  // Take "resubscription failed" actions caused by the subscription not existing.
  function takeResubGoneSubscription (action) {
    return (
      action.type === actionTypes.RESUBSCRIPTION_FINISHED &&
      action.error &&
      // Magic number: 39 is KL's statusCode for "resource does not exist".
      action.payload.code === 39
    )
  }

  while (true) {
    const action = yield take([takeGoneNotification, takeResubGoneSubscription])

    // Dispatch an action to disconnect the websocket (and let the connectivity
    //      plugin know we expect it to be disconnected).
    const wsState = yield select(getConnectionState, platform)
    if (wsState.connected) {
      yield disconnectWebsocket(undefined, platform)
    }

    // Dispatch a disconnect finished action to trigger "user disconnected" logic.
    // Ignore the GONE notification if an unsubscribe operation is pending as the unsubscribe saga will trigger the subscribe:change event already.
    const pendingOperation = yield select(getPendingOperation)
    if (pendingOperation && pendingOperation.operation === OPERATIONS.UNSUBSCRIBE) {
      continue
    }

    const content =
      action.payload.notificationMessage && action.payload.notificationMessage.retryAfter
        ? { reason: DISCONNECT_REASONS.GONE, retryAfter: action.payload.notificationMessage.retryAfter }
        : { reason: DISCONNECT_REASONS.GONE }
    yield put(actions.unsubscribeFinished(content, platform))
  }
}

/**
 * Triggers onConnectionLost saga when a connectivity.WS_RECONNECT_FAILED actionType occurs
 * @method onConnectionLostEntry
 */
export function * onConnectionLostEntry () {
  yield takeEvery(connectivityActionTypes.WS_RECONNECT_FAILED, onConnectionLost)
}

/**
 * Handles lost connections from the connectivity plugin
 * @method onConnectionLost
 */
export function * onConnectionLost () {
  yield put(
    actions.unsubscribeFinished({
      reason: DISCONNECT_REASONS.LOST_CONNECTION
    })
  )
}
