// Redux-Saga
import { put } from 'redux-saga/effects'
// Events
import eventsMap from '../interface/events'
import { mapEvents } from '../../events/interface/actions'

import { update as updateConfig } from '../../config/interface/actions'
import { logManager } from '../../logs'

import { defaults } from 'lodash/fp'

import { sendMessage, receiveMessage } from './sagas'

// The interface to implement.
import pluginInterface from '../interface'

// Parse and/or Validate
import { validation as v8n, parse } from '../../../src/common/validation'

// Get the logger
const log = logManager.getLogger('MESSAGING')

const defaultOptions = {
  features: ['base']
}

const v8nValidation = v8n.schema({
  features: v8n.array().every.string()
})
const parseOptions = parse('messaging', v8nValidation)

/**
 * Factory function for the Link messaging plugin.
 * @method linkMessaging
 * @param  {Object} [options={}]
 * @param  {Array}  [options.features=['base']] - A list of the supported features of messaging
 * @return {Object} A plugin object.
 */
export default function linkMessaging (options = {}) {
  options = defaults(defaultOptions, options)
  parseOptions(options)

  if (options.features.length > 1 || options.features[0] !== defaultOptions.features[0]) {
    log.warn('Link messaging is not compatible with add-on ' + 'features. Reverting to base messaging.')
    options.features = defaultOptions.features
  }
  log.info('Messaging features in use: ' + options.features)

  function * init () {
    yield put(updateConfig(options, pluginInterface.name))
    yield put(mapEvents(eventsMap))
  }

  const capabilities = ['simpleMessagingOnly', 'onlyInternalMessaging']

  return {
    sagas: [sendMessage, receiveMessage],
    capabilities,
    init,
    api: pluginInterface.api,
    name: pluginInterface.name,
    reducer: pluginInterface.reducer,
    mixins: pluginInterface.mixins
  }
}
