import { name, reducer, createAPI } from '../interface'
import createMiddleware from './middleware'
import createOperations from './operations'
import createRequests from './requests'

/**
 * Call History plugin factory.
 * @method callHistoryFactory
 */
export default function callHistoryFactory (options = {}, bottle) {
  // Register the component factory functions to the bottle.
  bottle.factory('CallHistoryAPI', createAPI)
  bottle.factory('CallHistoryOperations', createOperations)
  bottle.factory('CallHistoryRequests', createRequests)

  return {
    name,
    capabilities: ['callHistory'],
    reducer,
    middleware: createMiddleware
  }
}
