// Sip Events plugin.
import pluginInterface from './interface'
import * as sagas from './sagas'
import events from './interface/events'

// Other plugins.
import { mapEvents } from '../events/interface/actions'

// Libraries.
import { put } from 'redux-saga/effects'
import { values } from 'lodash/fp'

/**
 * Sip Events Plugin Factory.
 * @method sipEvents
 * @return {Object} Sip Events plugin.
 */
export default function sipEvents () {
  const { name, api, reducer } = pluginInterface
  const capabilities = ['sipEvents']

  function * init () {
    yield put(mapEvents(events))
  }

  return {
    name,
    init,
    api,
    reducer,
    capabilities,
    sagas: values(sagas)
  }
}
