// Redux-Saga
import { put } from 'redux-saga/effects'
// Events
import eventsMap from '../interface/events'
import { mapEvents } from '../../events/interface/actions'

// The interface to follow.
import { api, reducer, name } from '../interface'

// State setters.
import { update as updateConfig } from '../../config/interface/actions'

// Utilities.
import { mergeValues } from '../../common/utils'
import { logManager } from '../../logs'

// Parse and/or Validate
import { validation as v8n, parse, enums } from '../../../src/common/validation'

import { connectFlow, setCredentialsEntry, onSubscriptionGone, onConnectionLostEntry, updateTokenSaga } from './sagas'

// Get the logger
const log = logManager.getLogger('AUTH')

/**
 * Configuration options for the Authentication feature.
 * @public
 * @name config.authentication
 * @memberof config
 * @instance
 * @param {Object} authentication Authentication configs.
 * @param {Object} authentication.server
 * @param {string} authentication.server.base Hostname of the server to be used for requests.
 * @param {string} [authentication.server.protocol='https'] Protocol to be used for requests.
 * @param {number} [authentication.server.port=443] Port to be used for requests.
 * @param {Object} Deprecated: See {@link config#config.subscription Subscription config} authentication.subscription
 * @param {string} Deprecated: See {@link config#config.subscription Subscription config} authentication.subscription.server Hostname of the server to be used for subscription requests.
 * @param {string} Deprecated: See {@link config#config.subscription Subscription config} [authentication.subscription.protocol=https] Protocol to be used for subscription requests.
 * @param {number} Deprecated: See {@link config#config.subscription Subscription config} [authentication.subscription.port=443] Port to be used for subscription requests.
 * @param {string} Deprecated: See {@link config#config.subscription Subscription config} [authentication.subscription.version=1] Version of the REST API to be used.
 * @param {number} Deprecated: See {@link config#config.subscription Subscription config} [authentication.subscription.expires=3600] Time duration, in seconds, until a subscription should expire.
 * @param {Array} Deprecated: See {@link services.subscribe subscribe}[authentication.subscription.service] Services to subscribe to for notifications.
 * @param {Object} Deprecated: See {@link config#config.subscription Subscription config} authentication.websocket
 * @param {string} Deprecated: See {@link config#config.subscription Subscription config} authentication.websocket.server Hostname of the server to be used for websocket notifications.
 * @param {string} Deprecated: See {@link config#config.subscription Subscription config} [authentication.websocket.protocol=wss] Protocol to be used for websocket notifications.
 * @param {number} Deprecated: See {@link config#config.subscription Subscription config} [authentication.websocket.port=443] Port to be used for websocket notifications.
 */

const defaultOptions = {
  server: {
    protocol: 'https',
    base: null,
    port: 443,
    version: '1' // not documented, but important
  }
}

// config validation
const v8nValidation = v8n.schema({
  server: v8n.schema({
    protocol: enums(['http', 'https']),
    base: v8n.string(),
    port: v8n.positive(),
    version: enums(['1']) // not documented, but important
  })
})
const parseOptions = parse('authentication', v8nValidation)

/**
 * On link authentication implementation factory.
 * @method authLink
 * @param {Object} options - Configuration options for authentication. See above.
 * @return {Object} plugin - An authentication plugin.
 */
export default function authLink (options = {}) {
  if (options.subscription || options.websocket) {
    log.warn('Authentication configuration is being replaced, see docs. Please update to the config.')
  }

  // For backwards compatibility we check if the server is defined under subscription
  if (options.subscription) {
    if (!options.server) {
      options.server = {}
    }

    options.server.base = options.server.base || options.subscription.server
    options.server.protocol = options.server.protocol || options.subscription.protocol
    options.server.port = options.server.port || options.subscription.port
  }

  options = mergeValues(defaultOptions, options)
  parseOptions(options)

  if (!options.server.base) {
    log.error('No server configuration provided. Please provide proper authentication configurations.')
  }

  function * init () {
    // Send the provided options to the store.
    // This will be `state.config[name]`.
    yield put(updateConfig(options, name))
    yield put(mapEvents(eventsMap))
  }

  const capabilities = ['userCredentialsAuth', 'hmacTokenAuth', 'bearerAccessTokenAuth', 'accessTokenAuthCIM']

  return {
    sagas: [connectFlow, setCredentialsEntry, onSubscriptionGone, onConnectionLostEntry, updateTokenSaga],
    capabilities,
    init,
    api,
    reducer,
    name
  }
}
