import { overEvery, overSome, matches, map } from 'lodash/fp'
// Constants
import { platforms } from './constants'

export { matches }
export const and = (...operands) => overEvery(operands)
export const or = (...operands) => overSome(operands)
export const type = type => matches({ type })
export const platform = platform => matches({ meta: { platform } })
export const types = (...types) => overSome(map(type, types))

export const link = platform(platforms.LINK)
