// Proxy Plugin.
import pluginInterface from './interface'
import initializeProxy from './proxyStack'
import * as sagas from './sagas'
import events from './interface/events'

// Other plugins.
import { mapEvents } from '../events/interface/actions'

// Helpers.
import { autoRestart } from '../common/utils'
import { logManager } from '../logs'

// Libraries.
import initialize, { getWebRTCSupportCapabilities } from 'kandy-webrtc'
import { isFunction, values } from 'lodash/fp'
import { fork, put } from 'redux-saga/effects'

const log = logManager.getLogger('PROXY')

/**
 * Proxy Plugin factory.
 * The Proxy Plugin is responsible for setting up the webRTC /
 *    Media Proxy within the SDK. This includes replacing the
 *    webRTC stack with a Proxy stack, providing an interface
 *    for an application to configure the Proxy stack, and,
 *    in general, bringing all of the Proxy components together.
 * @method proxyPlugin
 * @return {Object} Plugin components.
 */
export default function proxyPlugin () {
  const { mediaDevices, peerConnection } = getWebRTCSupportCapabilities()
  if (!mediaDevices || !peerConnection) {
    log.warn(
      'Calls are not supported on this platform due to lack of WebRTC support. Proxy APIs will not be available.'
    )
    return
  }

  // Initialize a real webRTC stack.
  const webRTC = initialize()
  // Initialize the Proxy stack. The real webRTC stack is needed for
  //    when proxy-mode is disabled.
  const proxy = initializeProxy(webRTC)

  function * init ({ webRTC }) {
    yield put(mapEvents(events))

    // Make sure that webRTC is the Proxy Stack.
    if (!webRTC.setProxyMode || !isFunction(webRTC.setProxyMode)) {
      log.error('Cannot setup Proxy Plugin: Proxy Stack not found.')
      return
    }

    // Wrap the sagas in a function that provides them with the proxy stack.
    const wrappedSagas = values(sagas).map(saga => {
      return autoRestart(() => saga(webRTC))
    })

    // Run all of the sagas.
    for (const saga of wrappedSagas) {
      yield fork(saga)
    }
  }

  return {
    name: pluginInterface.name,
    api: pluginInterface.api,
    reducer: pluginInterface.reducer,
    init,
    capabilities: ['mediaProxy'],
    shared: {
      // Share the Proxy stack with the factory. The intention is that this
      //    will replace the webRTC stack the Webrtc plugin previously shared.
      webRTC: proxy
    }
  }
}
