import { name, reducer, createAPI } from '../interface'
import createOperations from './operations'
import createRequests from './requests'

/**
 * Users service plugin factory.
 * Provides the SDK with the 'Users' feature.
 * @method usersFactory
 */
export default function usersFactory (options = {}, bottle) {
  // Register the component factory functions to the bottle.
  bottle.factory('UsersAPI', createAPI)
  bottle.factory('UsersOperations', createOperations)
  bottle.factory('UsersRequests', createRequests)

  const capabilities = ['addContactAsFriend', 'selfInfoAsUserProfile', 'limitedSearch']
  return {
    capabilities,
    name,
    reducer
  }
}
