import { actionChannel, take, put, select } from 'redux-saga/effects'
// Messaging
import * as actionTypes from '../interface/actionTypes'
import { messageActions } from '../interface/actions'
import { getConversations } from '../interface/selectors'
// Auth
import { getConnectionInfo } from '../../auth/interface/selectors'
import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'
import * as P from '../../predicates'
// Request
import request from '../../request/effects'
// Error
import BasicError, { messagingCodes } from '../../errors'
// Logs
import { logManager } from '../../logs'

const log = logManager.getLogger('MESSAGING')

/**
 * Link send message saga.
 * Performs the workflow of sending a message using SPiDR/Link.
 * @method sendMessage
 */
export function * sendMessage () {
  const sendMessageChannel = yield actionChannel([actionTypes.SEND_MESSAGE])
  while (true) {
    const action = yield take(sendMessageChannel)
    const destination = action.payload.destination[0]
    const parts = action.payload.message.parts
    const timestamp = action.payload.message.timestamp
    const { server, username } = yield select(getConnectionInfo)

    const response = yield request({
      url: `${server.protocol}://${server.server}:${server.port}/rest/version/${server.version}/user/${username}/instantmessage`,
      method: 'POST',
      body: JSON.stringify({
        imRequest: {
          charset: 'UTF-8',
          toUrl: destination,
          message: parts[0].text,
          type: 'A2'
        }
      })
    })

    if (response.error) {
      let error
      if (response.payload.body) {
        // Handle errors from the server.
        const { statusCode } = response.payload.body.imResponse
        log.debug(`Failed to send message with status code ${statusCode}.`)

        error = new BasicError({
          code: messagingCodes.SEND_MESSAGE_FAIL,
          message: `Failed to send message. Code: ${statusCode}.`
        })
      } else {
        // Handler errors from the request helper.
        const { message } = response.payload.result
        log.debug('Send message request failed', message)

        error = new BasicError({
          code: messagingCodes.SEND_MESSAGE_FAIL,
          message: `Send message request failed: ${message}.`
        })
      }
      yield put(
        messageActions.sendMessageFinish({
          sender: username,
          destination: [destination],
          type: 'im',
          parts: parts,
          timestamp: timestamp,
          error
        })
      )
    } else if (response.payload.body.imResponse && response.payload.body.imResponse.messageId) {
      yield put(
        messageActions.sendMessageFinish({
          sender: username,
          destination: [destination],
          type: 'im',
          parts: parts,
          timestamp: timestamp,
          messageId: response.payload.body.imResponse.messageId
        })
      )
    } else {
      // Unknown error scenario.
    }
  }
}
/**
 * Link receive message saga.
 * Performs the workflow of receiving messages over the SPiDR websocket.
 * @method receiveMessage
 */
export function * receiveMessage () {
  // Redux-saga take() pattern.
  // Using predicates directly doesn't work... so wrap it.
  // TODO: Fix this properly.
  function receiveMessagePattern (action) {
    return P.and(P.type(NOTIFICATION_RECEIVED)(action) && P.link)(action)
  }

  while (true) {
    const action = yield take(receiveMessagePattern)

    if (action.payload.notificationMessage) {
      if (action.payload.notificationMessage.eventType === 'IM') {
        const message = action.payload.notificationMessage.imnotificationParams.msgText
        const sender = action.payload.notificationMessage.imnotificationParams.primaryContact.split('sip:')[1]
        const messageId = action.payload.notificationMessage.eventId
        const conversations = yield select(getConversations)

        // Search for an existing conversation that matches this message's sender.
        const convo = conversations.find(conv => {
          return conv.destination[0] === sender
        })

        if (convo) {
          yield put(
            messageActions.messageReceived(
              [sender],
              [{ mimeType: 'text/plain', type: 'text', text: message }],
              messageId,
              sender,
              Date.now(),
              {
                newConversation: false,
                type: 'im'
              }
            )
          )
        } else {
          // Add the new message to the convo in the store
          yield put(
            messageActions.messageReceived(
              [sender],
              [{ mimeType: 'text/plain', type: 'text', text: message }],
              messageId,
              sender,
              Date.now(),
              {
                newConversation: true,
                type: 'im'
              }
            )
          )
        }
      }
    }
  }
}
