/**
 * The Proxy state has changed.
 * Indicates either a change in the mode or the channel.
 * @public
 * @memberof proxy
 * @event proxy:change
 */
export const PROXY_CHANGE = 'proxy:change'

/**
 * An error has occurred with a Proxy operation.
 * @public
 * @memberof proxy
 * @event proxy:error
 * @param {Object} params
 * @param {Object} params.error An error object.
 */
export const PROXY_ERROR = 'proxy:error'
