/**
 * Send DTMF tones for a call.
 * @method sendDTMF
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * sendDtmfEntry (deps) {
  /**
   * Use `takeLeading` to avoid multiple operations happening at the same time.
   *    This is an issue for in-band DTMF, which would cause the second and all
   *    subsequent sendDtmf operations to fail if the first operation has not
   *    yet finished.
   */
  yield takeLeading(actionTypes.SEND_DTMF, sendDtmf, { ...deps })
}

/**
 * Call notifications.
 */

/**
 * Handle a Link "call incoming" notification.
 * @method incomingCallNotification
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * incomingCallNotification (deps) {
  /**
   * Intercept the Link-specific notification, ensuring parameters are given
   *    to the Callstack sagas into a generic format.
   * @method linkIncomingCall
   * @param {Object} action An "incoming call notification" action.
   */
  function * linkIncomingCall (action) {
    const message = action.payload.notificationMessage

    const notificationParams = message.callNotificationParams || {}

    // Massage data into a generic format, instead of Link-specific.
    const params = {
      // The remote SDP offer included with the notification (if any).
      sdp: message.sessionParams.sdp,
      // ID that the server uses to identify the session.
      wrtcsSessionId: message.sessionParams.sessionData,
      // Remote participant information.
      remoteName: notificationParams.callerName,
      remoteNumber: notificationParams.callerDisplayNumber,
      // Where the call was sent
      calleeNumber: notificationParams.calleeDisplayNumber,
      // Custom Parameters
      customParameters: message.customParameters
    }

    // Pass the incoming call parameters to the Callstack for handling.
    yield call(incomingCall, { ...deps, requests }, params, action.meta.channel)
  }

  // Redux-saga take() pattern.
  function incomingCallPattern (action) {
    return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === 'call'
  }
  yield takeEvery(incomingCallPattern, linkIncomingCall)
}

/**
 * Handle a Link "session progress" notification.
 * @method sessionProgressNotification
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * sessionProgressNotification (deps) {
  function progressPattern (action) {
    return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === 'sessionProgress'
  }

  /**
   * Intercept the notification before its sent to the Callstack. We do this
   *    to ensure the data is in a generic format.
   * @method parseSessionProgress
   */
  function * parseSessionProgress (action) {
    const message = action.payload.notificationMessage

    const notificationParams = message.callNotificationParams || {}

    // Massage data into a generic format, instead of Link-specific.
    const params = {
      sdp: message.sessionParams.sdp,
      wrtcsSessionId: message.sessionParams.sessionData,
      remoteName: notificationParams.remoteName,
      remoteNumber: notificationParams.remoteDisplayNumber,
      customParameters: message.customParameters
    }

    yield call(receiveEarlyMedia, deps, params)
  }

  yield takeEvery(progressPattern, parseSessionProgress)
}

/**
 * Handle a call status websocket notification.
 * @method callStatusNotification
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * callStatusNotification (deps) {
  function statusUpdatePattern (status) {
    return function statusPattern (action) {
      return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === status
    }
  }

  function * parseStatusNotification (action) {
    const message = action.payload.notificationMessage
    const { eventType } = message
    const { sessionData: wrtcsSessionId, reasonText, statusCode } = message.sessionParams

    const targetCall = yield select(getCallByWrtcsSessionId, wrtcsSessionId)
    if (!targetCall) {
      log.info('A remote operation is being requested on a call that no longer exists.')
    }

    const remoteInfo = yield call(getRemoteParticipant, message)

    const params = {
      wrtcsSessionId,
      reasonText,
      statusCode,
      // Remote participant's information.
      ...remoteInfo
    }
    if (eventType === 'callEnd') {
      yield call(callStatusUpdateEnded, deps, params)
    } else if (eventType === 'sessionComplete') {
      yield call(sessionStatusUpdateEnded, { ...deps, requests }, params)
    } else if (eventType === 'ringing') {
      params.customParameters = message.customParameters
      yield call(callStatusUpdateRinging, deps, params)
    } else if (eventType === 'sessionFail') {
      yield call(callStatusUpdateFailed, deps, params)
    }
  }

  // Handle specific call statuses
  yield takeEvery(statusUpdatePattern('ringing'), parseStatusNotification)
  yield takeEvery(statusUpdatePattern('callEnd'), parseStatusNotification)
  yield takeEvery(statusUpdatePattern('sessionComplete'), parseStatusNotification)
  yield takeEvery(statusUpdatePattern('sessionFail'), parseStatusNotification)
}

/**
 * Handle a `callCancel` notification from Link.
 * @method callCancelNotification
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * callCancelNotification (deps) {
  // Redux-saga take pattern.
  function callCancelPattern (action) {
    return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === 'callCancel'
  }

  /**
   * Handle Link-specific behaviours before passing the notification off to the
   *    Callstack.
   * @method handleCallCancel
   * @param  {Object} action
   */
  function * handleCallCancel (action) {
    // Get the current call
    const { sessionData } = action.payload.notificationMessage.sessionParams
    const currentCall = yield select(getCallByWrtcsSessionId, sessionData)

    /*
     * The backend sends a "callCancel" notification immediately after answering
     *    (and rejecting, forwarding) a call. The backend's intention for this
     *    is to notify other user subscriptions (eg. same user on another
     *    device) that the call has been handled, so the call is "cancelled" for
     *    this subscription. But they send it as a broadcast notification to all
     *    subscriptions, even the one that handled the call.
     *
     * We need to ignore this notification for the subscription that handled the
     *    call, but process it for any other subscriptions. We do this in two
     *    ways:
     *      1) If the Call state indicates we are obviously the device that
     *        answered it (eg. the Call is connected), ignore it.
     *      2) If a flag is set on the Call that indicates we are in-progress
     *        to handle it, ignore it.
     *    This handles receiving the callCancel before and after the in-progress
     *        operation has finished for both regular and slow-start calls.
     */

    if (
      currentCall &&
      currentCall.state !== CALL_STATES.RINGING &&
      currentCall.state !== CALL_STATES.EARLY_MEDIA &&
      currentCall.state !== CALL_STATES.INITIATED
    ) {
      // Don't process the notification if the call isn't "ringing".
      log.debug(`Received call cancel notification when state is ${currentCall.state}. Ignoring.`)
      return
    }

    if (currentCall.isHandling) {
      // Don't process the notification if we are currently handling the call.
      log.debug('Received call cancel notification while handling is in-progress. Ignoring.')
      return
    }

    const params = {
      wrtcsSessionId: sessionData
    }

    // Now process the notification after making sure we don't want to ignore it.
    yield call(callCancelled, deps, params)
  }

  yield takeEvery(callCancelPattern, handleCallCancel)
}

/**
 * Handle receiving remote offer notifications in a Link format.
 * Uses properties in the notification to create a standardized data object to
 *    be used by the Callstack.
 * @method receiveRemoteOffer
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * receiveRemoteOffer (deps) {
  /**
   * Intercept the Link-specific notification, ensuring parameters are given
   *    to the Callstack sagas into a generic format.
   * @method parseOfferNotification
   * @param {Object} action
   */
  function * parseOfferNotification (action) {
    const message = action.payload.notificationMessage
    const { sessionData: wrtcsSessionId } = message.sessionParams

    const targetCall = yield select(getCallByWrtcsSessionId, wrtcsSessionId)
    if (!targetCall) {
      log.info('A remote operation is being requested on a call that no longer exists.')
      return
    }

    const remoteInfo = yield call(getRemoteParticipant, message)

    // Pull-out the parameters into a standard format for the Callstack.
    const params = {
      wrtcsSessionId: message.sessionParams.sessionData,
      sdp: message.sessionParams.sdp,
      ...remoteInfo,
      customParameters: message.customParameters
    }

    // Pass the call parameters to the Callstack for handling.
    yield call(parseCallRequest, { ...deps, requests }, params)
  }

  // take() pattern for "update call w/ offer" notifications.
  function receiveOfferPattern (action) {
    return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === 'startCallUpdate'
  }

  yield takeEvery(receiveOfferPattern, parseOfferNotification)
}

/**
 * Handle receiving remote answer notifications in a Link format.
 * Uses properties in the notification to create a standardized data object to
 *    be used by the Callstack.
 * @method receiveRemoteAnswer
 * @param {Object} deps             Dependencies to be injected.
 * @param {Object} deps.webRTC      The WebRTC stack.
 */
export function * receiveRemoteAnswer (deps) {
  /**
   * Intercept the Link-specific notification, ensuring parameters are given
   *    to the Callstack sagas into a generic format.
   * @method parseAnswerNotification
   * @param {Object} action
   */
  function * parseAnswerNotification (action) {
    const message = action.payload.notificationMessage
    const { sessionData: wrtcsSessionId } = message.sessionParams

    const targetCall = yield select(getCallByWrtcsSessionId, wrtcsSessionId)
    if (!targetCall) {
      log.info('A remote operation is being requested on a call that no longer exists.')
      return
    }

    const remoteInfo = yield call(getRemoteParticipant, message)

    // Pull-out the parameters into a standard format for the Callstack.
    const params = {
      wrtcsSessionId: message.sessionParams.sessionData,
      sdp: message.sessionParams.sdp,

      retryAfter: message.sessionParams.retryAfter,
      message: message.sessionParams.reasonText,
      code: message.statusCode,

      // Remote participant information.
      ...remoteInfo,

      customParameters: message.customParameters
    }

    // Pass the call parameters to the Callstack for handling.
    yield call(parseCallResponse, { ...deps, requests }, params)
  }

  // take() pattern for "update call with answer" notifications.
  function receiveAnswerPattern (action) {
    return action.type === NOTIFICATION_RECEIVED && action.payload.notificationMessage.eventType === 'respondCallUpdate'
  }

  yield takeEvery(receiveAnswerPattern, parseAnswerNotification)
}

