/**
 * Handles updating TURN information after the user connects.
 * @method setTurnCredentials
 */
export function * setTurnCredentials () {
  // Check that the action is a successful subscription that includes
  //    TURN information from the server.
  function hasTurnCredentials (action) {
    return (
      !action.error &&
      ((action.type === CONNECT_FINISHED &&
        action.payload.subscription &&
        action.payload.subscription.turnCredentials) ||
        (action.type === SUBSCRIBE_FINISHED &&
          action.payload.subscriptions &&
          action.payload.subscriptions.length &&
          action.payload.subscriptions[0].turnCredentials))
    )
  }

  yield takeEvery(hasTurnCredentials, supportSagas.setTurnCredentials)
}

