// Redux-Saga
import { cancel, call, fork, put, select, takeEvery, take, actionChannel } from 'redux-saga/effects'

// Auth
import * as actions from '../interface/actions'
import * as actionTypes from '../interface/actionTypes'
import { DISCONNECT_REASONS } from '../constants'

// Other plugins
import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'
import * as subscribeActions from '../../subscription/interface/actions'
import * as subscribeActionTypes from '../../subscription/interface/actionTypes'
import * as connectivityActionTypes from '../../connectivity/interface/actionTypes'
import { getSubscriptionInfo } from '../../subscription/interface/selectors'

// State selectors
import {
  getAuthConfig,
  getUserInfo as selectUserInfo,
  getSubscriptionInfo as getAuthSubscription
} from '../interface/selectors'

// Constants
import { platforms } from '../../constants'

// Helpers
import { normalizeServices } from '../../common/utils'

// Errors
import BasicError, { authCodes } from '../../errors'

// Libraries
import base64 from 'base-64'
import utf8 from 'utf8'

// Logging
import { logManager } from '../../logs'

// Get the logger
const log = logManager.getLogger('AUTH')

// This is an Link plugin.
const platform = platforms.LINK

// Taker-saga that watches for "set credential" actions.
export function * setCredentialsEntry () {
  yield takeEvery(actionTypes.SET_CREDENTIALS, setCredentials)
}

/**
 * Link connect saga. This Saga is in charge of the flow for connecting and disconnecting.
 * Handles the workflow of connecting or disconnecting to SPiDR.
 * @method connectFlow
 */
export function * connectFlow () {
  while (true) {
    // Wait for a CONNECT action.
    const action = yield take(actionTypes.CONNECT)

    // Fork off and try to connect
    const task = yield fork(connect, action)

    // Take the actions for if an error or disconnect occurs.
    const finishOrError = yield take([actionTypes.CONNECT_FINISHED, actionTypes.DISCONNECT])

    /**
     * This if block covers 2 of the 3 flows of connect / disconnect.
     * 1. The first block covers what happens when we receive a DISCONNECT action before we finish connecting.
     *        IE DISCONNECT comes before a CONNECT_FINISHED.
     * 2. The second block covers what happens when we get a successful CONNECT_FINISHED event before any DISCONNECT events.
     * 3. The third scenario is not explicitly covered here. That scenario is when we received a CONNECT_FINISHED event with an error before any disconnect events.
     *      In this case, there is nothing that we explicitly have to do, since a CONNECT_FINISHED event with an error doesn't place anything into the state.
     *      Therefore there is no real cleanup for us to do in this scenario. We then want to wait for the next CONNECT ( which we do via the next iteration of the while loop).
     **/
    if (finishOrError.type === actionTypes.DISCONNECT) {
      log.debug('Cancelling task')
      yield cancel(task)
    } else if (finishOrError.type === actionTypes.CONNECT_FINISHED && !finishOrError.error) {
      const disconnectAction = yield take([actionTypes.DISCONNECT, actionTypes.DISCONNECT_FINISHED])

      // if disconnect has finished, we dont need to do a teardown of auth state, or disconnect the websocket, so reset connectFlow
      if (disconnectAction.type === actionTypes.DISCONNECT_FINISHED) {
        continue
      }
      log.debug('Disconnecting')
      yield call(disconnect)
    }
  }
}

/**
 * Link connect function.
 * Performs the workflow of connecting to SPiDR.
 * @method connect
 */
export function * connect (action) {
  // Set connection info
  const { credentials, options } = action.payload
  yield put(actions.setCredentials(credentials))

  // Wait for action SET_CREDENTIALS_FINISH
  const setCredentialsFinishOrError = yield take(actionTypes.SET_CREDENTIALS_FINISH)

  if (setCredentialsFinishOrError.error) {
    log.info('Failed to apply credentials. Error: ', setCredentialsFinishOrError.payload)
    yield put(
      actions.connectFinished({
        error: setCredentialsFinishOrError.payload
      })
    )
    return
  }
  log.info('Successfully applied credentials.')

  // Retrieve the connection info.  We need this to support old configurations
  const authConfig = yield select(getAuthConfig)

  const service = authConfig.subscription.service

  if (service) {
    const userInfo = yield select(selectUserInfo)
    if (userInfo && (userInfo.username || userInfo.accessToken)) {
      // Normalize services array
      const services = normalizeServices(service)

      log.info('Subscribing to services: ', services)
      yield put(subscribeActions.subscribe(services, options))

      // Wait for action SUBSCRIBE_FINISH
      const subscribeFinishOrError = yield take(subscribeActionTypes.SUBSCRIBE_FINISHED)
      if (subscribeFinishOrError.error) {
        log.info('Failed to subscribe to services. Error: ', subscribeFinishOrError.payload.error)
        yield put(actions.connectFinished({ error: subscribeFinishOrError.payload.error }))
        return
      }
      log.info('Successfully subscribed to services.')
    }
  } else {
    // No services found in config
    yield put(
      actions.connectFinished({
        error: new BasicError({
          message: 'No services found in configuration.',
          code: authCodes.MISSING_SERVICE
        })
      })
    )
    return
  }

  yield put(
    actions.connectFinished(
      // SET_CREDENTIALS_FINISH action will store connection and userInfo
      // SUBSCRIBE_FINISH action will store subscription
      {},
      platform
    )
  )
}

/**
 * Link disconnect function.
 * Performs the actions for actually disconnecting from SPiDR.
 * @method disconnect
 */
export function * disconnect () {
  log.info('Unsubscribing from services.')

  // The backwards compatible `disconnect` is to unsubscribe from all services.
  //    So get the list of current services to give to the new subscription flow.
  const subscription = yield select(getSubscriptionInfo)
  const { service, notificationType } = subscription[0]
  yield put(subscribeActions.unsubscribe(service, notificationType))

  yield take([subscribeActionTypes.UNSUBSCRIBE_FINISHED])

  // Successfully unsubscribed, now finish disconnecting
  log.info('Successfully unsubscribed from services. Disconnecting.')
  yield put(actions.disconnectFinished())
}

// Functionality for setting / validating user credentials.
export function * setCredentials (action) {
  // Retrieve the connection info.
  const config = yield select(getAuthConfig)

  // Common request options, to be used for all subsequent requests after connect.
  const requestOptions = {
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json'
    }
  }

  // Determine what extra information needs to be added to the request.
  const { username, password, authname, hmacToken, bearerAccessToken, accessToken } = action.payload
  if (hmacToken) {
    // Only need the hmacToken in the x-token header when subscribing.
    log.info('Setting the info for hmacToken scenario.')
    requestOptions.headers['x-token'] = hmacToken
  } else if (username && accessToken) {
    log.info('Setting the info for accessToken CIM Auth scenario.')
    requestOptions.queryParams = {
      token: accessToken
    }
  } else if (authname && password) {
    log.info('Setting the info for authname/password scenario.')
    requestOptions.headers.Authorization = 'Basic ' + base64.encode(utf8.encode(authname + ':' + password))
  } else if (bearerAccessToken && username) {
    log.info('Setting the info for bearerAccessToken/username scenario.')
    requestOptions.headers.Authorization = `Bearer ${bearerAccessToken}`
  } else if (username && password) {
    log.info('Setting the info for username/password scenario.')
    requestOptions.headers.Authorization = 'Basic ' + base64.encode(utf8.encode(username + ':' + password))
  } else {
    log.info('Unexpected credentials scenario.')
    const error = new BasicError({
      message: 'This credentials signature is not valid.',
      code: authCodes.INVALID_CREDENTIALS
    })
    yield put(actions.setCredentialsFinished({ error }, platform))
    return
  }

  // Gather information to provide externally.
  const userInfo = {
    username
  }

  // Gather information to provide internally, to other plugins.
  const connection = {
    server: {
      server: config.server.base,
      port: config.server.port,
      protocol: config.server.protocol,
      version: config.server.version
    },
    username,
    bearerAccessToken,
    // Request options that all other (Link) plugins will use.
    requestOptions,
    accessToken
  }

  yield put(
    actions.setCredentialsFinished(
      {
        userInfo,
        connection
      },
      platform
    )
  )
}

/**
 * Saga that handles a "subscription gone" notification.
 * Treat the notification as a forced disconnect.
 * @method onSubscriptionGone
 */
export function * onSubscriptionGone () {
  // Redux-saga take() pattern.
  // Take notifications about the subscription being 'gone'.
  function takeGoneSubscription (action) {
    return (
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.notificationMessage &&
      action.payload.notificationMessage.eventType === 'gone'
    )
  }

  while (true) {
    yield take(takeGoneSubscription)

    /*
     * Subscription plugin will handle the 'gone' notification as well and make
     * sure the websocket is disconnected.  Here we only disconnect the user with
     * reason GONE to trigger the auth:changed event for the client.
     * This is for backwards compatibility with 3.x connect flow.  If you
     * are using the new method of connecting, using setCredentials or setToken
     * and subscribe, then you should be using the 'subscription:change' event
     * instead of the 'auth:change' event.
     */

    const subscription = yield select(getAuthSubscription)

    /*
     * If there is any subscription info stored in authentication state, that means the old
     *    authentication method was used (ie. connect API). When using the old method, the
     *    subscription/websocket and authentication are tied together. So when the websocket is
     *    lost, we want to remove the authentication info as well. Dispatching a disconnectFinished
     *    action will clear that from state.
     * This should only be the case when:
     *  - using 3.X auth
     *  - using 4.X auth with the backwards-compatible flow (ie. connect API).
     * This check prevents the authentication state (ie. user info and request info) being
     *    cleared when using the independent subscription flow.
     * Reference: KAA-2538 & KJS-55
     */

    if (subscription) {
      // Dispatch a disconnect finished action to trigger "user disconnected" logic.
      yield put(actions.disconnectFinished({ reason: DISCONNECT_REASONS.GONE }))
    }
  }
}

/**
 * Triggers onConnectionLost saga when a connectivity.WS_RECONNECT_FAILED actionType occurs.
 * This is only here to maintain backwards compatibility with the auth connect flow. Moving
 * forward, this will be handled by the subscription plugin instead.
 * @method onConnectionLostEntry
 */
export function * onConnectionLostEntry () {
  yield takeEvery(connectivityActionTypes.WS_RECONNECT_FAILED, onConnectionLost)
}

/**
 * Handles lost connections from the connectivity plugin
 * @method onConnectionLost
 */
export function * onConnectionLost () {
  const subscription = yield select(getAuthSubscription)

  /*
   * If there is any subscription info stored in authentication state, that means the old
   *    authentication method was used (ie. connect API). When using the old method, the
   *    subscription/websocket and authentication are tied together. So when the websocket is
   *    lost, we want to remove the authentication info as well. Dispatching a disconnectFinished
   *    action will clear that from state.
   * This should only be the case when:
   *  - using 3.X auth
   *  - using 4.X auth with the backwards-compatible flow (ie. connect API).
   * This check prevents the authentication state (ie. user info and request info) being
   *    cleared when using the independent subscription flow.
   * Reference: KAA-2538 & KJS-55
   */
  if (subscription) {
    yield put(actions.disconnectFinished({ reason: DISCONNECT_REASONS.LOST_CONNECTION }))
  }
}

/**
 * The logic for updating the store's tokens by the user.
 * @method updateTokenSaga
 */
export function * updateTokenSaga () {
  const tokenChannel = yield actionChannel([actionTypes.REFRESH_TOKENS])
  while (true) {
    const action = yield take(tokenChannel)

    const { username, accessToken, bearerAccessToken } = action.payload.credentials

    let requestOptions = {}

    if (bearerAccessToken && username && !accessToken) {
      // For subscription requests using an OAuth Token, attach it as a header.
      requestOptions = {
        headers: {
          Authorization: `Bearer ${bearerAccessToken}`
        }
      }
    } else if (accessToken && username && !bearerAccessToken) {
      // Ensure the extra options for requests is updated with the new token.
      requestOptions.queryParams = {
        token: action.payload.credentials.accessToken
      }
    } else {
      log.debug('updateToken called without the necessary parameters')
    }

    log.info('Refreshing token for platform: ', platform)
    yield put(
      actions.refreshTokensFinished(
        {
          connection: {
            ...action.payload.credentials,
            requestOptions
          }
        },
        platform
      )
    )
  }
}
