// Proxy Plugin.
import * as actions from '../interface/actions'
import { getProxyState } from '../interface/selectors'

// Other plugins.
import { getActiveCalls } from '../../call/interfaceNew/selectors'
import { logManager } from '../../logs'
import { getLevel } from '../../logs/interface/selectors'
import { devicesChanged } from '../../webrtc/interface/actions/devices'

// Helpers.
import BasicError from '../../errors'

// Libraries.
import { call, fork, put, select, take } from 'redux-saga/effects'
import { eventChannel } from 'redux-saga'

const log = logManager.getLogger('PROXY')

/**
 * Sets the proxy mode for the Proxy Stack.
 * @method setProxyMode
 * @param {Object} webRTC The Proxy Stack.
 * @param {Object} action
 */
export function * setProxyMode (webRTC, action) {
  log.info(`Setting proxy mode to ${action.payload.value}.`)
  // Check to see if there are any on-going calls.
  const calls = yield select(getActiveCalls)

  if (calls.length > 0) {
    log.info('Cannot change proxy mode while there are on-going calls.')
    yield put(
      actions.setProxyModeFinish({
        error: new BasicError({
          code: 'proxy:3',
          message: 'Cannot change proxy mode while there are on-going calls.'
        })
      })
    )
    return
  }

  const value = action.payload.value
  const result = yield call([webRTC, 'setProxyMode'], value)

  const response = {
    value
  }
  // If the mode wasn't set, define an error to be included
  //    in the finish action.
  if (!result) {
    log.info(`Failed to set proxy mode to ${value}.`)
    response.error = new BasicError({
      code: 'proxy:1',
      message: `Failed to set proxy mode to ${value}.`
    })
  } else {
    log.info(`Finished setting proxy mode to ${value}.`)
  }

  yield put(actions.setProxyModeFinish(response))

  // After Proxy mode is changed, manually update devices to ensure they are
  //    from the correct machine.
  const devices = yield call([webRTC.managers.devices, 'checkDevices'])
  if (devices.microphone && devices.camera && devices.speaker) {
    log.debug('Updating available media devices based on proxy mode change.', devices)
    // Only update state with the devices if its an actual device object.
    // If the Remote SDK is not initialized yet, it will return garbage.
    yield put(devicesChanged(devices))
  }
}

/**
 * Sets the channel for the Proxy Stack.
 * @method setChannel
 * @param {Object} webRTC The Proxy Stack.
 * @param {Object} action
 */
export function * setChannel (webRTC, action) {
  log.info('Setting channel for use in proxy mode.')
  // Check to see if there are any on-going calls.
  const calls = yield select(getActiveCalls)

  if (calls.length > 0) {
    log.info('Cannot set channel while there are on-going calls.')
    yield put(
      actions.setChannelFinish({
        error: new BasicError({
          code: 'proxy:3',
          message: 'Cannot set channel while there are on-going calls.'
        })
      })
    )
    return
  }

  const channel = action.payload.channel
  const result = yield call([webRTC, 'setChannel'], channel)

  const response = {}
  // If the channel wasn't set, define an error to be included
  //    in the finish action.
  if (!result) {
    log.info('Failed to set channel for proxy mode.')
    response.error = new BasicError({
      code: 'proxy:2',
      message: 'Failed to set proxy channel.'
    })
  } else {
    log.info('Finished setting channel for proxy mode.')
    // Channel was successfully set.
    // Listen for messages received from the channel.
    yield fork(handleMessages, webRTC)
  }

  yield put(actions.setChannelFinish(response))
}

/**
 * Handles (non-reply) messages received from the Client Host.
 * @method handleMessages
 * @param  {Object}  webRTC The webRTC stack.
 */
export function * handleMessages (webRTC) {
  // Setup an event channel to bring the messages into redux-saga's context.
  const messageChannel = eventChannel(emit => {
    /**
     * Set the listener function on the Proxy Stack for
     *    receiving messages. It simply "emits" the entire
     *    messages into the event channel.
     */
    webRTC.onMessage((messageId, data) => {
      emit({ messageId, data })
    })

    return () => {
      // Unsubscribe by removing any listener function.
      webRTC.onMessage(() => {})
    }
  })

  while (true) {
    const { messageId, data } = yield take(messageChannel)

    // Ignore messages from the channel if we're not in Proxy mode.
    const proxyState = yield select(getProxyState)
    if (!proxyState.proxyMode) {
      log.debug(`Not in Proxy mode, ignoring previous message (${messageId}).`)
      continue
    }

    // If the received message is an event, dispatch it.
    if (data.event) {
      log.debug(`Received event from remote webrtc stack (${data.event.type}).`)
      yield put(data.event)
    } else {
      log.debug(`Received unknown message type; ignoring message (${messageId}).`, data)
    }
  }
}

export function * initializeRemote (webRTC, action) {
  log.info('Initializing the Remote SDK for proxy mode.')

  // Get the current local log levels for the loggers that the remote end also uses.
  //    Send this as part of the init command, so the two sides are in-sync.
  const webrtcLevel = yield select(getLevel, 'WEBRTC')
  const proxyLevel = yield select(getLevel, 'PROXY')
  const channelLevel = yield select(getLevel, 'CHANNEL')
  const logLevels = { WEBRTC: webrtcLevel, PROXY: proxyLevel, CHANNEL: channelLevel }

  const result = yield call([webRTC, 'initialize'], action.payload.config, logLevels)

  if (result.error) {
    log.info('Failed to initialize the Remote SDK.')
    yield put(actions.initializeRemoteFinish({ error: result.error.message }))
  } else {
    log.info('Finished initializing the Remote SDK.')
    yield put(actions.initializeRemoteFinish({ browser: result.browser }))
  }
}

/**
 * Functionality saga for "update proxy log level" actions.
 * @method updateProxyLevel
 * @param  {Object} webRTC The WebRTC stack.
 * @param  {Object} action A "log level change" action.
 */
export function * updateProxyLevel (webRTC, action) {
  const { proxyMode } = yield select(getProxyState)

  // If the PROXY log level was changed while in Proxy mode, update the PROXY
  //    logger on the Remote SDK.
  if (proxyMode && action.payload.PROXY) {
    log.info('Updating proxy log level on the remote end of channel.')
    /*
     * Special-case: The PROXY logger isn't managed by the WebRTC stack's
     *   log manager, but we pretend that it is. We do this because we need to
     *   send this "command" to the remote end, so we piggy-back on how the
     *   ProxyStack sends its "logger commands" (to update the remote WebRTC
     *   stack's loggers). The Remote SDK needs to check for this special-case
     *   to handle this properly. If it doesn't, then this will do nothing.
     */
    const logger = yield call([webRTC.managers.logs, 'getLogger'], 'PROXY')
    yield call([logger, 'setLevel'], action.payload.PROXY)
  }

  // Same thing as above for the CHANNEL logger.
  if (proxyMode && action.payload.CHANNEL) {
    log.info('Updating channel log level on the remote end of channel.')
    const logger = yield call([webRTC.managers.logs, 'getLogger'], 'CHANNEL')
    yield call([logger, 'setLevel'], action.payload.CHANNEL)
  }
}
