// The interface to follow.
import { api, reducer, name } from '../interface'

// Redux-Saga
import { put } from 'redux-saga/effects'

// Events
import eventsMap from '../interface/events'
import { mapEvents } from '../../events/interface/actions'

// State setters.
import { update as updateConfig } from '../../config/interface/actions'

// Utilities.
import { mergeValues } from '../../common/utils'

// Parse and/or Validate
import { validation as v8n, parse } from '../../../src/common/validation'

import {
  registerServices,
  subscriptionFlow,
  extendSubscription,
  onSubscriptionGone,
  onWebsocketOverridden,
  onConnectionLostEntry
} from './sagas'

/**
 * Configuration options for the Subscription feature.
 * @public
 * @name config.subscription
 * @memberof config
 * @instance
 * @param {Object} subscription Subscription config.
 * @param {number} [subscription.expires=3600] The amount of time (in seconds) for which to keep subscription up and alive.
 * @param {Object} subscription.websocket
 * @param {string} subscription.websocket.server Hostname of the server to be used for websocket notifications.
 * @param {string} [subscription.websocket.protocol=wss] Protocol to be used for websocket notifications.
 * @param {Number} [subscription.websocket.port=443] Port to be used for websocket notifications.
 */
const defaultOptions = {
  expires: 3600, // seconds
  websocket: {
    protocol: 'wss',
    port: 443
  }
}

// config validation
const v8nValidation = v8n.schema({
  expires: v8n.positive(),
  websocket: v8n.schema({
    server: v8n.string(),
    protocol: v8n.string(),
    port: v8n.positive()
  })
})

const parseOptions = parse('subscription', v8nValidation)
/**
 * Link subscription implementation factory.
 * @method createSubscriptionPlugin
 * @param {Object} options - Configuration options for subscription. See above.
 * @return {Object} plugin - An subscription plugin.
 */
export default function createSubscriptionPlugin (options = {}) {
  options = mergeValues(defaultOptions, options)
  parseOptions(options)

  function * init () {
    // Send the provided options to the store.
    // This will be 'state.config[name]'.
    yield put(updateConfig(options, name))
    yield put(mapEvents(eventsMap))
  }

  return {
    sagas: [
      registerServices,
      subscriptionFlow,
      extendSubscription,
      onSubscriptionGone,
      onWebsocketOverridden,
      onConnectionLostEntry
    ],
    init,
    capabilities: ['link_subscription'],
    api,
    reducer,
    name
  }
}
