/**
 * The messaging feature revolves around a 'conversation' namespace. It is responsible to store the conversations
 * and its messages, and return conversation objects when requested.
 *
 * See the "Conversation" and "Message" sections of the documentation for more details.
 *
 *
 * Messaging functions are all part of the 'conversation' namespace. Ex: client.conversation.get('id').
 *
 * @public
 * @namespace conversation
 */

import { convoActions } from './actions'
import { getConversations as selectConversations, findConversation } from './selectors'
import { logManager, API_LOG_TAG } from '../../logs'

// Retrieve logger
const log = logManager.getLogger('MESSAGING')

export default function api (context) {
  const messagingApi = {
    // TODO: Revise this API's argument, since it is using names that are defined by CIM  they are not very descriptive.
    /**
     * Attempts to retrieve a list of conversations that the current user is a part of.
     * These conversations can then be retrieved from the store using get().
     *
     * @public
     * @param {Object} [options] An optional configuration object to query for more specific results.
     * If no object is passed, all threads will be retrieved.
     * @param {string} [options.touched] The unix timestamp in seconds representing the date from which
     *  to return any threads that have changed. Can also pass the string literal "lastcheck", resulting in
     *  the back-end making use of the most recent date value provided in a previous request
     * @param {string} [options.type] Limit results to one of: "internal", "sms", "group" or "unknown".
     * @param {string|number} [options.thread] Limit results to one thread specified by its thread handle.
     * @memberof conversation
     * @requires fetchConversations
     * @method fetch
     */
    fetch: function (options = {}) {
      log.debug(API_LOG_TAG + 'conversation.fetch: ', options)

      context.dispatch(convoActions.fetchConversations(options))
    },
    /**
     * Get a conversation object matching the user ID provided in the 'destination' parameter.
     * If successful, the event 'conversations:change' will be emitted.
     *
     * This API will retrieve a conversation already existing in the store.
     *
     * @public
     * @memberof conversation
     * @requires onlyInternalMessaging
     * @method get
     * @param {string} destination The destination for messages created in this conversation. This will
     * be a user's sip address.
     * @param {Object} [options] An optional configuration object to query for more specific results.
     * If this object is not passed, the function will query for "im" conversation with that recipient.
     * @param {string} [options.type] The type of conversation to retrieve. Can be one of "im", "sms" or "other".
     * @returns {conversation.Conversation} A Conversation object matching the passed destination, otherwise undefined is returned.
     */
    /**
     * Get a conversation object matching the user IDs provided in the 'destination' parameter.
     * If successful, the event 'conversations:change' will be emitted.
     * Multi-user conversations have a destination comprised of multiple user IDs.
     *
     * @public
     * @memberof conversation
     * @requires multiUserConversation
     * @method get
     * @param {Array} destination An array of destinations for messages created in this conversation.
     * These will be a user's sip address.
     * @param {Object} [options] An optional configuration object to query for more specific results.
     * If this object is not passed, the function will query for "im" conversations associated with those destinations.
     * @param {string} [options.type] The type of conversation to retrieve. Can be one of "im", "sms" or "other".
     * @returns {conversation.Conversation} A Conversation object matching the passed destination, otherwise undefined is returned.
     */
    /**
     * Get a conversation object matching the user ID provided in the 'recipient' parameter.
     *
     * This API will retrieve a conversation already existing in the store.
     *
     * @public
     * @memberof conversation
     * @requires internalAndSmsMessaging
     * @method get
     * @param {string} recipient The destination for messages created in this conversation. This
     * will be a user's sip address.
     * @param {Object} [options] An optional configuration object to query for more specific results.
     * If this object is not passed, the function will query for "im" conversation with that recipient.
     * @param {string} [options.type] The type of conversation to retrieve. Can be one of "im", "sms" or "other".
     * @returns {Object} A Conversation object matching the passed recipient, otherwise undefined is returned.
     */
    get: function (recipient, options = { type: 'im' }) {
      log.debug(API_LOG_TAG + 'conversation.get: ', recipient, options)
      const destination = Array.isArray(recipient) ? [...recipient] : [recipient]
      let description = 'Conversation'
      let messages
      let id

      const conversation = findConversation(context.getState(), destination, options.type)
      if (conversation) {
        if (options.type === conversation.type) {
          description = conversation.description
          messages = conversation.messages
          id = conversation.id

          return context.primitives.Conversation({
            destination,
            type: options.type,
            id: id,
            description,
            messages: messages,
            lastMessage: conversation.lastMessage,
            isTypingList: conversation.isTypingList,
            lastReceived: conversation ? conversation.lastReceived : undefined,
            lastPull: conversation ? conversation.lastPull : undefined
          })
        } else {
          log.info(
            'Conversation found with matching destination, but conversation type does not match the type requested. Please specify the appropriate type, or call client.conversation.create() for a new type of conversation'
          )
        }
      } else {
        log.info(
          'Requested conversation not in state. Please use the create function if you would like a new conversation object.'
        )
      }
      return undefined
    },
    /**
     * Create and return a new conversation object. Any messages being sent through this conversation
     * object will be sent to the destination provided
     *
     * @public
     * @memberof conversation
     * @requires internalAndSmsMessaging
     * @method create
     * @param {string} recipient
     * @param options
     * @returns {Object} a Conversation object
     */
    /**
     * Create and return a new conversation object. Any messages being sent through this conversation
     * object will be sent to the destinations provided
     *
     * @public
     * @memberof conversation
     * @requires multiUserConversation
     * @method create
     * @param {Array} recipient An array of destinations for messages created in this conversation. These will be a user's sip address.
     * @param {string} type The type of conversation to create. Can be one of "im", "sms", "group" or "other"
     * @param options
     * @returns {Object} a Conversation object
     */
    create: function (recipient, options = { type: 'im' }) {
      log.debug(API_LOG_TAG + 'conversation.create: ', recipient, options)
      const destination = Array.isArray(recipient) ? recipient : [recipient]
      const prevConv = findConversation(context.getState(), destination, options.type)

      if (!prevConv) {
        context.dispatch(convoActions.createConversation(destination, options))
      }

      return context.primitives.Conversation({
        destination,
        type: options.type
      })
    },
    /**
     * Returns all conversations currently tracked by the SDK
     *
     * @public
     * @memberof conversation
     * @requires internalAndSmsMessaging
     * @requires onlyInternalMessaging
     * @method getAll
     * @returns {Array<conversation.Conversation>} An array of conversation objects.
     */
    getAll: function () {
      log.debug(API_LOG_TAG + 'conversation.getAll')
      const conversations = selectConversations(context.getState())

      // Map conversation Objects to Conversation-Mixin Objects
      return conversations.map(conversation => context.primitives.Conversation(conversation))
    }
  }
  return { conversation: messagingApi }
}
